.GalleryPopup{
    width: 100%;
    height: calc(100vh - 48px);
    z-index: 999;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.6);
    top: 48px;
    display: none;
}

.GalleryPopup-content{
    width: fit-content;
    height: fit-content;
    position: relative;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    overflow: hidden;
}

.GalleryPopup-closeIcon{
    width: 24px;
    height: 24px;
    top: 10px;
    left: calc(100% - 34px);
    position: absolute;
    z-index: 99;
    cursor: pointer;
}

.GalleryPopup-image{
    width: 100%;
    height: auto;
    object-fit: contain;
    max-width: 1100px;
    position: relative;
}

@media screen and (min-width:751px) {
    .GalleryPopup.open{
        display: block;
    }
}
